import React from "react";
import axios from "axios";

import { withRouter } from "react-router-dom";

class companySettings extends React.Component {
	
	constructor(props) {
        super(props);
		
		const today = new Date()
		
		this.state = {
			currentDate: today,
			
			messages: "Hello, there is nothing yet here...",
			
			dropMessage: 'Select Company=>',
			currentCompany: "None",
		
			companyList: [],

        };
		
		
	};

	backButton = () => {
		this.props.history.goBack()
	}

	getCompanyList = () => {

		// We will change this to its own function eventually...
		if (this.props.authToken == null) {
			console.log("Not logged in")
			return false
		}
		
		const config = {
			headers: { Authorization: `JWT ${this.props.authToken}` }
		};
		
		// This is bad, as it gets ALL companies...
		// But we SHOULD save it with the permissions...
		// So I need to make this better...
		axios.get(this.props.APIHost +"/getUsersCompanies", config)
		.then( 	res => {
				// Should respond with a 1 length thing
				if (res.data.length > 0) {
					// Normal behaviour
					console.log("Got the list of companies")
					//console.log(res)
					
					// Companies this user is 'owner' of
					//console.log(res.data[0].companyOwner)
					
					// Merge these three into a single set for viewing for now?
					let adding = []
					
					var index;
					for (index in res.data[0].companyOwner) {
						let name = res.data[0].companyOwner[index]
						if ( !adding.includes(name) ) {
							adding.push(name)
							//console.log(name)
						}							
					}					
					this.setState({companyList: adding})
				}
				else {
					// This should not trigger
				}
			})
			.catch( err => {
				console.log(err)
			});
	}
	
	getCompanyData = (targetCompany) => {
		if (this.props.authToken == null) {
			console.log("Not logged in")
			return false
		}
		
		const config = {
			headers: { Authorization: `JWT ${this.props.authToken}` }
		};
		
		axios.get(this.props.APIHost +"/getCompanyData?reqComp="+targetCompany+"", config)
		.then( 	res => {
				// Should respond with a 1 length thing
				if (res.data.length > 0) {
					// Normal behaviour
					console.log("Got the company data!")
					//console.log(res)
					
					// Merge these three into a single set for viewing for now?
					console.log(res.data[0].summaryViewAccess)
					console.log(res.data[0].companyName)
					console.log(res.data[0].resultsSendTargets)
					console.log(res.data[0].privateViewingPerms)
					console.log(res.data[0].governedUsers)
					
					//this.setState({companyList: adding})
				}
				else {
					// This should not trigger
				}
			})
			.catch( err => {
				console.log(err)
				
				// I should do a force logout check...
			});
	}
	
	triggerCompanySwap = (event) => {
		this.setState({
			dropMessage: event.target.value, 
			currentCompany: event.target.value
		})
		
	}

	render() {
		
		var dropDownInternal = []
		
		let index;
		for (index in this.state.companyList) {
			let comp = this.state.companyList[index]
			dropDownInternal.push( 
				<button className="dropdown-item" key={index} onClick={this.triggerCompanySwap} value={ comp }>
					{comp}
				</button> 
			)
		}
		
		if ( !this.state.currentCompany === "None") {
			console.log("We have a valid company to search!")
		}
		
		return (
			<div className="companySett">
				<div className="container">

					<div className="row">
						<div className="col-sm-3 m-2">	
							<button className="btn btn-primary" onClick={this.backButton}>
								Go Back
							</button>
						</div>
						<div className="col-sm m-2">	
							Page Title Test!
						</div>
					</div>
				
					<div className="row">
						<div className="col">
							<div className="dropdown">
							  <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
							  {this.state.dropMessage}
							  </button>
							  <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
								{dropDownInternal}
							  </div>
							</div>
						</div>
						<div className="col">
							<button onClick={this.getCompanyList}> Populate Company List </button>
						</div>
					</div>
					
					<div className="row">
						<div className="col">
							<div className="card">
								<div className="card-header">
									<p>
										Edit Company name
									</p>
								</div>
								<div className="card-body">
									<p className="card-text">
										Body
									</p>
								</div>
							</div>
						</div>
						<div className="col">
							<div className="card">
								<div className="card-header">
									<p>
										Private/Publix
									</p>
								</div>
								<div className="card-body">
									<p className="card-text">
										Body
									</p>
								</div>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="col">
							<div className="card">
								<div className="card-header">
									<p>
										Users Notified:
									</p>
								</div>
								<div className="card-body">
									<p className="card-text">
										Body
									</p>
								</div>
							</div>
						</div>
						<div className="col">
							<div className="card">
								<div className="card-header">
									<p>
										Viewing Privilages:
									</p>
								</div>
								<div className="card-body">
									<p className="card-text">
										Body
									</p>
								</div>
							</div>
						</div>
						<div className="col">
							<div className="card">
								<div className="card-header">
									<p>
										Target Users:
									</p>
								</div>
								<div className="card-body">
									<p className="card-text">
										Body
									</p>
								</div>
							</div>
						</div>
					</div>
					
				</div>
			</div>
		)
	}
}

export default withRouter(companySettings);